// 从文件获取json数据
var data = require('./common/data.json');
export default {
	data(){
		return {
			videourl:''
		}
	},
	onLoad() {
		uni.getStorage({
			key:'videourl',
			success: (res) => {
				this.videourl = res.data
			}
		})
	},
	onUnload() {
		uni.removeStorage({
			key:'videourl',
			success() {}
		})
	}
}
